const React = require('react');
const InjectChildComponent = require('./Inject.js');
const { Link } = require('react-router-dom');

class SectionAbout extends React.Component {
  constructor() {
    super();
  }

  render() {
    return (
      <div className="wrap-about">
        <section className="section-about">
          <article className="section-about__article">
              <h1 className="section-about__article__title">
                Acerca del evento
                <div className="lower-bar"></div>
              </h1>

              <p className="section-about__article__copy">
                Hackd Conf, es el evento anual llevado a cabo por la Hackd Foundation y Partners, que rene a entusiasta emprendedores y desarrolladores durante dos das en la ciudad de Quibd.
                <br/>
                Ms de 2 aos impactando el ecosistema de base tecnolgica del Choc,
                llegando a ms de <b>200 personas</b> con <b>22 charlas</b> impartidas, <b>17 talleres sobre 4 lenguajes de programacina</b> adems de charlas sobre el internet de las cosas y <b>programacin de hardware</b>.
              </p>
          </article>

          <article className="section-about__article wrap-about-image">
            <div className="section-about__article-image"></div>
          </article>


        </section>
       </div>
    )
  }
}

module.exports = InjectChildComponent(SectionAbout, []);



